// Bedag-CI 2021 Violettes Theme

$primary: #6e50be; // Bedag-Violett 100%
$success: #96cd6e; // Bedag-Grün 100%
$warning: #ffc107;
$info: #373795;
$light: #f8f9fa; // Bootstrap Gray-100
$dark: #57476b; // Bedag-Dunkelviolett 80%

$theme-colors: (
  'primary': $primary,
  'success': $success,
  'warning': $warning,
  'info': $info,
  'light': $light,
  'dark': $dark,
);
