@import '~prismjs/themes/prism-okaidia.css';

a.highlight:before {
  @extend .fas;
  @extend .fa-angle-right;
  @extend .fa-fw;
}

ul.related-elements {
  list-style: none;
  padding-left: 1.5em;

  li :first-child:before {
    @extend .fas;
    @extend .fa-angle-right;
    @extend .fa-fw;
    padding-right: 0.3em;
  }
}

.autocomplete-result {
  min-width: 20em;
}

/* Some example classes for the color page */
.color-nav-hover {
  background-color: rgba($primary, 0.05);
}

.color-nav-selected {
  background-color: rgba($primary, 0.15);
}

.color-list-select {
  background-color: rgba($primary, 0.1);
}

.color-list-select-hover {
  background-color: rgba($primary, 0.2);
}
